import { useState } from "react";
import "./upload.css";

const Upload = () => {

  const [data, setData] = useState([]);
  
  const [input, setInput] = useState({ file: "" });



  const handleFileChange = (e) => {
    const selectedFile = e.target.files[0];
    if (selectedFile && selectedFile.size <= 2 * 1024 * 1024) {
      const newInput = { file: selectedFile };
      setInput(newInput);
      setData((prevData) => [...prevData, newInput]);
    } else {
      alert("File size exceeds 2MB limit. Please select a smaller file.");
    }
  };

  const formatFileSize = (size) => {
    if (size === 0) return "0 Bytes";
    const k = 1024;
    const sizes = ["Bytes", "KB", "MB", "GB", "TB"];
    const i = parseInt(Math.floor(Math.log(size) / Math.log(k)));
    return Math.round(size / Math.pow(k, i), 2) + " " + sizes[i];
  };

  const handleMergeClick = () => {
    const fileDataArray = [];

    if (data.length > 0) {
      data.forEach(async (item) => {
        const uploadedFile = item.file;

        const fileData = {
          fileName: uploadedFile.name,
          fileSize: formatFileSize(uploadedFile.size),
          base64Content: await readFileContent(uploadedFile),
        };

        fileDataArray.push(fileData);

        console.log("Array of Objects:", fileDataArray);
      });
    } else {
      console.log("No files uploaded yet.");
    }
  };

  const readFileContent = (file) => {
    return new Promise((resolve) => {
      const reader = new FileReader();
      reader.onload = () => {
        const base64Content = reader.result.split(",")[1]; // Base64 content
        resolve(base64Content);
      };
      reader.readAsDataURL(file);
    });
  };


  const handleDrop = (e) => {
    e.preventDefault();
    const files = e.dataTransfer.files;
    for (let i = 0; i < files.length; i++) {
      const file = files[i];
      if (file.size <= 2 * 1024 * 1024) {
        const newInput = { file };
        setData((prevData) => [...prevData, newInput]);
      } else {
        alert(
          `File ${file.name} size exceeds 2MB limit. Please select a smaller file.`
        );
      }
    }
  };

  const handleDragOver = (e) => {
    e.preventDefault();
  };
  const handleDelete = (index) => {
    setData((prevData) => prevData.filter((_, i) => i !== index));

  };


  const row = data.map((item, index) => {
    return (
      <tr key={index}>

        <td className="name">{item.file.name}</td>
        <td className="size">{formatFileSize(item.file.size)}</td>
        <td className="remove">
          <button
            type="button"
            onClick={() => handleDelete(index)}
            className="btn"
          >
            <i class="fa fa-trash" aria-hidden="true"></i>
          </button>
        </td>
      </tr>
    )
  })

  return (
    <>
      <div class="card">
        <div className="card-upload" >
          <header className="card-header">
            <div className="table-style" >
              <table className="table">
                <tbody>
                  {row}
                </tbody>
              </table>
            </div>
          </header>

          <div className="file-upload">

            <label htmlFor="file-upload" onDragOver={handleDragOver}
              onDrop={handleDrop} className="button">
              <div>
                <p>Drop  the files here or Click to Upload</p>
                <p className="bold">browse the files</p>
              </div>

              <input
                id="file-upload"
                type="file"
                onChange={handleFileChange}
              />
            </label>
          </div>
          <footer className="card-footer">
            <div className="merge-button">
              <button onClick={handleMergeClick} className="button1">
                Merge
              </button>
            </div>
          </footer>
        </div>

      </div>

    </>

  );

};

export default Upload;