// LoginLayout.js
import React from 'react';

const LoginLayout = ({ children }) => {
  return (
    <div style={{ backgroundColor: '#eee', color: '#000' }}>
      <header className="content has-text-left has-background-warning-dark"style={{ height: '60px', }} >
       <h1>PDF</h1>
      </header>
      <main>{children}</main>
      <footer className="footer has-background-warning-dark"style={{ height: '60px', padding: '10px 0', boxSizing: 'border-box' }}>
          <h1> &copy; {new Date().getFullYear()} PDF. All rights reserved.</h1>
        </footer>
    </div>
  );
};

export default LoginLayout;
